<template>
  <main class="UseCase">
    <section class="UseCase__info">
      <div class="UseCase__info--image">
        <img :src="activeCase.image">
      </div>
      <div class="UseCase__info-main">
        <div class="UseCase__info-title">
          {{ activeCase.title }}
        </div>
        <div class="UseCase__info-subtitle" v-html="activeCase.subtitle"></div>
      </div>
      <div class="UseCase__info-cases">
        {{ usecases.items }}
        <card v-for="(item, name) in activeCase.items" :key="name" :title="getCardTitle(name)" :content="item"></card>
      </div>
      <div class="UseCase__info-capsule">
        <img class="UseCase__info-capsule--img" :src="activeCase.capsule"/>
        <img class="UseCase__info-capsule--figure" src="/img/background-shapes/elipse-blue.svg"/>
      </div>
    </section>
    <section class="UseCase__capsules">
      <h2 class="UseCase__others-title">
        {{ activeCase.title }} Capsules
      </h2>
      <slider>
        <template v-slot:content>
          <capsule
            v-for="(item, index) in activeCase.capsules"
            :key="index"
            :image="item.image"
            :name="item.name"
            :volume="item.volume"
          />
        </template>
      </slider>
    </section>
    <section class="UseCase__others">
      <h2 class="UseCase__others-title">
        Other Use Cases
      </h2>
      <slider>
        <template v-slot:content>
          <use-case-card
              v-for="(item, index) in otherCases"
              :key="index"
              :image="item.image"
              :title="item.title"
              :description="item.description"
              :url="item.path"
          />
        </template>
      </slider>
    </section>
  </main>
</template>
<script>
import { USECASES } from "../assets/useCases";
import UseCaseCard from "../components/items/UseCaseCard";
import Card from "../components/common/Card";
import Slider from "../components/items/Slider";
import Capsule from "../components/items/Capsule";

export default {
  name: 'UseCase',
  components: {
    'slider': Slider,
    'use-case-card': UseCaseCard,
    'card': Card,
    'capsule': Capsule
  },
  data: () => ({
    usecases: USECASES
  }),
  computed: {
    activeCase() {
      return this.usecases.find(item => item.value === this.$route.params.usecase)
    },
    otherCases() {
      return this.usecases.filter(item => item.value !== this.activeCase.value)
    }
  },
  methods: {
    getCardTitle(key) {
      if (key === 'personalization') return 'Personalization'
      else if (key === 'activeIngredients') return 'Active Ingredients'
      else if (key === 'naturalClean') return 'Natural & Clean'
      else if (key === 'foodIngredients') return 'Food Ingredients'
    },
  }
}
</script>
